
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import PlateType from '../../../js/enum.js'
import MoneyCell from '../components/MoneyCell.vue'
import { getYMDFormateTime } from "../../../js/utility.js"
import { mapState } from 'vuex'


export default {
name: 'moneyhistory', //回款历史
components: {
Group,
MoneyCell,
XButton
},
computed: {
...mapState([
'array'
]),
PlateType() {
return PlateType;
},
},
data() {
return {}
},
mounted() {















},
methods: {
click() {

this.$router.push({
name: 'addrelate',
params: {
mainid: this.$route.params.mainid,
id: this.$route.params.id,
type: PlateType.Contract,
addtype: '5'
}
});
}
}
}




